import fetch from 'node-fetch';
import { version } from '../package.json';
const USER_AGENT = `algolia_crawler_github_actions/${version}`;
export class CrawlerApiClient {
    constructor({ crawlerUserId, crawlerApiBaseUrl, crawlerApiKey, }) {
        this.crawlerUserId = crawlerUserId;
        this.crawlerApiKey = crawlerApiKey;
        this.crawlerApiBaseUrl = crawlerApiBaseUrl;
    }
    get basicAuthToken() {
        return `Basic ${Buffer.from(`${this.crawlerUserId}:${this.crawlerApiKey}`).toString('base64')}`;
    }
    static async __handleResponse(res) {
        if (res.ok) {
            try {
                return (await res.json());
            }
            catch (err) {
                console.log('Body', await res.text());
                throw new Error('Cant decode success body');
            }
        }
        const body = await res.text();
        throw new Error(`${res.status}: ${res.statusText}\n${body}`);
    }
    async createCrawler(name, jsonConfig) {
        const body = {
            name,
            config: jsonConfig,
        };
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers`, {
            method: 'POST',
            headers: {
                Authorization: this.basicAuthToken,
                'Content-Type': 'application/json',
                'User-Agent': USER_AGENT,
            },
            body: JSON.stringify(body),
        });
        return CrawlerApiClient.__handleResponse(res);
    }
    async updateCrawler({ id, name, jsonConfig, }) {
        const body = {
            name,
            config: jsonConfig,
        };
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers/${id}`, {
            method: 'PATCH',
            headers: {
                Authorization: this.basicAuthToken,
                'Content-Type': 'application/json',
                'User-Agent': USER_AGENT,
            },
            body: JSON.stringify(body),
        });
        return CrawlerApiClient.__handleResponse(res);
    }
    async getCrawlers({ itemsPerPage, page, name, appId, }) {
        const searchParams = {};
        if (itemsPerPage)
            searchParams.itemsPerPage = itemsPerPage;
        if (page)
            searchParams.page = page;
        if (name)
            searchParams.name = name;
        if (appId)
            searchParams.appId = appId;
        const qs = Object.keys(searchParams)
            .map((k) => `${encodeURIComponent(k)}=${encodeURIComponent(searchParams[k])}`)
            .join('&');
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers${qs ? `?${qs}` : ''}`, {
            headers: {
                Authorization: this.basicAuthToken,
                'User-Agent': USER_AGENT,
            },
        });
        return CrawlerApiClient.__handleResponse(res);
    }
    async updateConfig(id, partialJsonConfig) {
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers/${id}/config`, {
            method: 'PATCH',
            headers: {
                Authorization: this.basicAuthToken,
                'Content-Type': 'application/json',
                'User-Agent': USER_AGENT,
            },
            body: JSON.stringify(partialJsonConfig),
        });
        return CrawlerApiClient.__handleResponse(res);
    }
    async getConfig(id) {
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers/${id}?withConfig=true`, {
            headers: {
                Authorization: this.basicAuthToken,
            },
        });
        return CrawlerApiClient.__handleResponse(res);
    }
    async getStatus(id) {
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers/${id}`, {
            headers: {
                Authorization: this.basicAuthToken,
                'User-Agent': USER_AGENT,
            },
        });
        return CrawlerApiClient.__handleResponse(res);
    }
    async getURLStats(id) {
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers/${id}/stats/urls`, {
            headers: {
                Authorization: this.basicAuthToken,
                'User-Agent': USER_AGENT,
            },
        });
        return CrawlerApiClient.__handleResponse(res);
    }
    async reindex(id) {
        return await this.__triggerAction({ crawlerId: id, actionName: 'reindex' });
    }
    async run(id) {
        return await this.__triggerAction({ crawlerId: id, actionName: 'run' });
    }
    async pause(id) {
        return await this.__triggerAction({ crawlerId: id, actionName: 'pause' });
    }
    async __triggerAction({ crawlerId, actionName, }) {
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers/${crawlerId}/${actionName}`, {
            method: 'POST',
            headers: {
                Authorization: this.basicAuthToken,
                'Content-Type': 'application/json',
                'User-Agent': USER_AGENT,
            },
        });
        return CrawlerApiClient.__handleResponse(res);
    }
    async waitForTaskToComplete({ crawlerId, taskId, }) {
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers/${crawlerId}/tasks/${taskId}`, {
            headers: {
                Authorization: this.basicAuthToken,
                'User-Agent': USER_AGENT,
            },
        });
        const { pending } = (await res.json());
        if (pending) {
            await new Promise((resolve) => {
                setTimeout(resolve, 1000);
            });
            await this.waitForTaskToComplete({ crawlerId, taskId });
        }
    }
    async testUrl({ crawlerId, url, config, }) {
        const res = await fetch(`${this.crawlerApiBaseUrl}/crawlers/${crawlerId}/test`, {
            method: 'POST',
            headers: {
                Authorization: this.basicAuthToken,
                'Content-Type': 'application/json',
                'User-Agent': USER_AGENT,
            },
            body: JSON.stringify({ url, config }),
        });
        return (await res.json());
    }
}
